	methods: {
		handleTabClick(tabId) {
			if (tabId === 'service') {
				uni.redirectTo({ url: '/pages/designer/detail' })
			} else if (tabId === 'appointment') {
				uni.redirectTo({ url: '/pages/designer/booking' })
			} else if (tabId === 'works') {
				uni.redirectTo({ url: '/pages/designer/works' })
