				this.activeTab = tabId
			}
		},
		handleFilterClick(index) {
			this.reviewFilters.forEach((filter, i) => {
				filter.active = i === index
			})
		},
		handleMoreClick() {
			uni.navigateTo({ url: '/pages/designer/info' })
